.cart-items-container, .wishlist-items-container {
    display: flex;
    flex-wrap: wrap; 
    gap: 20px;
    justify-content: center;
    margin: 0 auto;
    flex-direction: column;
    padding: 0 20% 0 20%;

}

.wishlist-items-container .empty-quantity-placeholder{
    width: 100px;
    visibility: hidden;
}

.cartwishlist-item{
    
    border: 1px solid #ccc;
    padding: 15px;
    border-radius:5px;
    width: auto;
    box-shadow: 2px 2px 5px rgba(0,0,0,0.1);
}

.cartwishlist-item-wrapper{
    display: grid;
    grid-template-columns: 100px auto ; 
    grid-template-rows: auto auto;   
    align-items: center;   
    border: 1px solid #eee; /* Subtle border */
    padding: 10px; 
    margin-bottom: 15px;
}

.cartwishlist-item-wrapper p {  
    margin-top: -20px;  /* Control spacing above price */
    grid-column: 2;
}

.img-cartwishlistitem{
    height: 100px;
    width: 100px;
    object-fit:contain;
    border: 1px solid #ccc;
}
.quantity-control {
    display: flex;
    align-items: center;
    margin-top: 10px;
    justify-content: center;  
}


.quantity-control button {
    width: 30px;
    height: 30px;
    border: 1px solid #ccc;
}

.quantity {
    margin: 0 10px; /* Add spacing around quantity display */
} 


.item-exists-popup {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    padding: 20px;
    border: 1px solid #ccc;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    z-index: 1000; /* Adjust as needed to ensure it appears above other elements */
    max-width: 300px; /* Adjust the maximum width as needed */
    text-align: center;
  }
  
  .item-exists-popup p {
    margin-bottom: 10px;
  }
  
  .item-exists-popup div {
    cursor: pointer;
    color: #007bff;
    text-decoration: underline;
  }

  .remove-button {
    width: 160px;
    height: 40px;
    border-radius: 12px;
    border: none;
    background-color: #dc3545;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    overflow: hidden;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.103);
    position: relative;
    transition: background-color 0.5s ease, box-shadow 0.5s ease;
    margin: 0 auto; /* Center the button horizontally */
  }
  
  .remove-button:hover {
    background-color: #c82333;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.103);
  }
  
  .remove-button svg {
    height: 20px;
    width: 20px;
    margin-top: -2px;
  }
  
  .remove-button .text {
    height: 100%;
    width: fit-content;
    display: flex;
    align-items: center;
    justify-content: center;
    color: rgb(255, 255, 255);
    font-size: 1.04em;
    font-weight: 600;
    transition: transform 0.5s ease;
  }
  
  .remove-button .IconContainer {
    transform: translateX(-50px);
    border-radius: 40px;
    transition: transform 0.5s ease;
  }
  
  .remove-button:hover .IconContainer {
    transform: translateX(50px);
  }
  
  .remove-button:hover .text {
    transform: translate(10px, 0);
  }
  
  .remove-button:active {
    transform: scale(0.95);
    transition: transform 0.5s ease;
  }
  